.text-gradient {
    background: linear-gradient(270deg, #20dafb 0%, #7686e1 46.67%, #ad71f7 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    width: fit-content;
    zoom: 1.05;
}

h1 {
    position: relative;
    font-weight: 600;
    line-height: 100%;
    margin-bottom: 1.1rem;
    font-size: 3.2rem;
    z-index: 5;
}

.hero-hero {
    width: 100%;
    margin: auto;
    position: relative;
    transform: translateY(1.7rem);
}

.about {
    position: relative;
    color: #929EB0;
    margin-top: 1.5rem;
    z-index: 5;

}

.resume {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 9.5rem;
    height: 2.8rem;
    margin-top: 3.5rem;
    background: linear-gradient(270deg, #13ADC7 0%, #6978D1 66.67%, #945DD6 100%);
    box-shadow: 0px 10px 30px rgba(0, 178, 255, 0.35);
    border-radius: 60px;
    font-weight: 600;
    font-size: 0.87rem;
    color: #FFFFFF;
    outline: none;
    border: none;
}

.resume:hover {
    cursor: pointer;
    transform: scale(1.05);
    background: linear-gradient(270deg, #24dafa 0%, #8b9cfc 66.67%, #b980fe 100%);
}

.balls {
    position: fixed;
    top: -5rem;
    right: 3rem;
    background: transparent;
    transition: transform 0.1s linear !important;
}

.ball2 {
    position: fixed;
    top: 7rem;
    right: -6rem;
    background: transparent;
    transition: transform 0.15s linear !important;
}
.ball3 {
    position: fixed;
    top: -3.5rem;
    right: -3rem;
    background: transparent;
    transition: transform 0.2s linear !important;
    width: fit-content;
    height: fit-content;
}

.pp {
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 14.2rem;
    height: 14.2rem;
    box-shadow: 0px 20px 50px rgba(0, 178, 255, 0.25);
    border-radius: 200px;
    transition: transform 0.2s linear !important;
}

.pp-bg {
    margin-bottom: -27px;
    margin-right: 0.2px;
}