.projects-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    margin: 2rem 0;
    margin-bottom: 3rem;
}

.project {
    background: linear-gradient(#0F1624, #0F1624) padding-box,
        linear-gradient(270deg, #20dafb 0%, #7686e1 46.67%, #ad71f7 100%) border-box;
    border: 1px solid transparent;
    border-radius: 1.6rem;
    height: 32rem;
    display: flex;
    flex-direction: column;
    gap: 0.6rem;
    padding: 2.4rem;
    transition: all .2s;
}

.project-img {
    width: 100%;
    border-radius: 1rem;
}

.project-title {
    font-weight: 600;
    font-size: 1.34rem;
    margin-top: 1rem;
}

.project-desc {
    font-weight: 500;
    font-size: 0.9rem;
    color: #929EB0;
}

.project-actions {
    margin-top: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 1.5rem;
    padding: 0 0.5rem;
}

.project-preview {
    padding: 0.5rem;
    width: 47%;
    background: linear-gradient(270deg, #13ADC7 0%, #6978D1 66.67%, #a166e8 100%);
    text-decoration: none;
    border-radius: 100px;
    text-align: center;
}
.project-preview:hover {
    background: linear-gradient(270deg, #24dafa 0%, #8b9cfc 66.67%, #b980fe 100%);
}

.project-github {
    padding: 0.5rem;
    width: 53%;
    border: 2px solid rgba(255, 255, 255, 0.9);
    text-decoration: none;
    border-radius: 100px;
    text-align: center;
    opacity: 0.85;
}
.project-github:hover {
    border: 2px solid rgba(255, 255, 255);
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.19);
}

.project:hover *{
    transform: scale(1.04);
}
.project:hover{
    transform: scale(1.02);
}
.project:hover .project-img{
    transform: scale(1.1);
}