.navbar {
    position: fixed;
    z-index: 999999;
    background-color: #0f1624de;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 1rem 4rem 0.8rem 2rem;
    align-items: center;
    font-weight: 400;
}

.navlink:hover {
    color: #8ee2f1;
    cursor: pointer;
}

.navlink {
    font-weight: 400;
    transition: all 0.2s;
}

.navbar-left {
    font-size: 1.3rem;
    font-weight: 500;
}

.navbar-center, .navbar-right {
    display: flex;
    flex-direction: row;
    gap: 2.5rem;
}

.navbar-right {
    width: 9rem;
    justify-content: right;
}