a {
    text-decoration: none;
}

.contact-hero {
    font-weight: 500;
    font-size: 1.1rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0 1.6rem 0;
}

.contact-left {
    background: linear-gradient(270deg, #20dafb 0%, #7686e1 46.67%, #ad71f7 100%);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 600;
    font-size: 1.3rem;
    width: fit-content;
}

.contact-center>a {
    transition: all .2s;
}

.contact-center>a:hover {
    color: #88dbea;;
}