.tech-hero {
    width: 100%;
    padding-bottom: 3rem;
}

.tech {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin: 1.8rem 0;
    align-items: center;
}

.tech-progress {
    border-radius: 83px;
    height: 17px;
    background: linear-gradient(270deg, #13ADC7 0%, #6978D1 66.67%, #945DD6 100%);
    box-shadow: 0px 3px 15px rgba(19, 173, 199, 0.3);
    border-radius: 83px;
}

.tech-name {
    font-size: 1.1rem;
    margin-bottom: 0.6rem;
}

.tech-left {
    width: 100%;
}

.tech-right {
    margin-left: 4rem;
    font-size: 0.9rem;
}